<template>
  <v-container fluid pa-1 ma-0>
    <v-layout column>
      <v-flex v-for="item in list" :key="item.id" pa-0 ma-1>
        <v-card color="white">
          <v-flex xs12>
            <v-card-text class="font-weight-light">
              <v-layout row wrap>
                <v-flex shrink pa-2>
                  <v-avatar slot="activator" size="36px">
                    <v-img class="elevation-6"
                           src="https://avataaars.io/?avatarStyle=Transparent&topType=ShortHairShortCurly&accessoriesType=Prescription02&hairColor=Black&facialHairType=Blank&clotheType=Hoodie&clotheColor=White&eyeType=Default&eyebrowType=DefaultNatural&mouthType=Default&skinColor=Light"
                    />
                  </v-avatar>
                </v-flex>
                <v-flex shrink pa-2>
                  <span class="subheading mr-2" v-if="item.nickName">{{item.nickName}}</span>
                  <span class="red--text subheading mr-2" v-if="item.status===1">已禁用</span>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex shrink pa-2>
                  <v-icon small class="mr-1">phone</v-icon>
                  <span class=" mr-2">{{item.mobile}}</span>
                </v-flex>
                <v-flex shrink pa-2>
                  <v-icon small class="mr-1">email</v-icon>
                  <span class=" mr-2">{{item.email}}</span>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-flex>

          <v-card-actions class="pa-0">
            <v-spacer></v-spacer>
            <v-btn icon>
              <v-icon color="blue-grey darken-3" title="编辑">edit</v-icon>
            </v-btn>
            <v-btn icon>
              <v-icon color="blue-grey darken-3" title="删除">delete</v-icon>
            </v-btn>
            <v-btn icon>
              <v-icon color="blue-grey darken-3" title="增加">add</v-icon>
            </v-btn>
            <v-btn icon v-if="item.status===0">
              <v-icon color="blue-grey darken-3" title="禁用">report</v-icon>
            </v-btn>
            <v-btn icon v-if="item.status===1">
              <v-icon color="blue-grey darken-3" title="启用">flag</v-icon>
            </v-btn>
          </v-card-actions>

        </v-card>

      </v-flex>
      <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="0"/>
    </v-layout>

    <v-speed-dial bottom right fixed>
      <v-btn slot="activator" color="blue darken-2" dark fab>
        <v-icon>account_circle</v-icon>
        <v-icon>close</v-icon>
      </v-btn>

      <v-tooltip left>
        <v-btn fab dark small color="indigo" slot="activator" @click="dialog=!dialog">
          <v-icon>edit</v-icon>
        </v-btn>
        <span>Add Note</span>
      </v-tooltip>
      <v-tooltip left>
        <v-btn fab dark small color="yellow" slot="activator" @click="dialog=!dialog">
          <v-icon>edit</v-icon>
        </v-btn>
        <span>Add Log Entry</span>
      </v-tooltip>
      <v-tooltip left>
        <v-btn fab dark small color="green" slot="activator" @click="dialog=!dialog">
          <v-icon>add</v-icon>
        </v-btn>
        <span>Add and Activate New Task</span>
      </v-tooltip>
      <v-tooltip left>
        <v-btn fab dark small color="gray" slot="activator" @click="dialog=!dialog">
          <v-icon>add</v-icon>
        </v-btn>
        <span>Add New Inactive Task</span>
      </v-tooltip>
      <v-tooltip left>
        <v-btn fab dark small color="red" slot="activator">
          <v-icon>delete</v-icon>
        </v-btn>
        <span>Delete Task</span>
      </v-tooltip>

    </v-speed-dial>
    <v-dialog v-model="dialog" width="800px">
      <v-card>
        <v-card-title class="grey lighten-4 py-4 title">Add New</v-card-title>
        <v-container>
          <v-textarea v-model="message" full-width></v-textarea>
        </v-container>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn flat color="primary" @click="dialog = false">Cancel</v-btn>
          <v-btn flat @click="dialog = false">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>

</template>

<script>
  import {adminUserList} from '@/api/core-api'

  export default {
    data() {
      return {
        page: 1, // 默认载入第一页
        atThisPage: true, // 在使用了keep-alive包裹显示组件的情况下，需要判断当前激活的组件是不是此组件，是的话才加载数据
        list: [],
        dialog: false
      }
    },
    methods: {
      loadMore: function () {
        if (this.atThisPage) {
          let _data = {};
          adminUserList(_data).then((data) => {
            // 保存登录状态和信息
            this.list.concat(data.data.content);
          });
          this.page++
        }
      },
      filterData: function (startDate, endDate, mealType) {
        if (this.atThisPage) {
          let _data = {};
          adminUserList(_data).then((data) => {
            // 保存登录状态和信息
            this.list = data.data.content;
          });
        }
      }
    },
    created() {
      let _data = {};
      adminUserList(_data).then((data) => {
        // 保存登录状态和信息
        this.list = data.data.content;
      });
    },
    activated() {
      this.atThisPage = true
    },
    deactivated() {
      this.atThisPage = false
    }
  }
</script>



